<template>
  <h1>Reactions</h1>
  <div class="page">
    <table class="table-hover">   
      <tr v-for="item in list" :key="item">
        <td>
          <a :href="'/reactions/' + item.id">
            <div class="data">
              <img :src="item.URL" alt="image"><br>
              <p>Nom : {{ item.name }}</p>
              <p>Description : {{ item.description }}</p>
            </div>
          </a>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>

export default {
  name: 'listingActions',
  components: {
  },
  created() {
    var myHeaders = new Headers();
    myHeaders.append("token", localStorage.token);
    var requestOptions = {
      method: 'GET',
      headers: myHeaders,
      redirect: 'follow'
    };
    fetch(localStorage.server + "/area/listAR/reaction", requestOptions)
      .then(res => {
        res.json().then((ans) => {
            console.log();
            this.list = ans["list"];
          })
      })
      .catch(error => console.log('error', error));
  },
  data() {
    return {
      list: []
    }
  },
};
</script>

